// layout mixins - start

@mixin mkdfRelativeHolderLayout($vertical-align: middle) {
	position: relative;
	display: inline-block;
	width: 100%;
	vertical-align: $vertical-align;
}

@mixin mkdfAbsoluteHolderLayout() {
	position: absolute;
	display: block;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}

@mixin mkdfTableLayout() {
	position: relative;
	display: table;
	table-layout: fixed;
	height: 100%;
	width: 100%;
}

@mixin mkdfTableCellLayout($vertical-align: middle) {
	position: relative;
	display: table-cell;
	height: 100%;
	width: 100%;
	vertical-align: $vertical-align;
}

@mixin mkdfTypographyLayout($important: null) {
	color: inherit $important;
	font-family: inherit $important;
	font-size: inherit $important;
	font-weight: inherit $important;
	font-style: inherit $important;
	line-height: inherit $important;
	letter-spacing: inherit $important;
	text-transform: inherit $important;
}

@mixin mkdfBlockquote($important : null) {
	@include mkdfRelativeHolderLayout();
	margin: 10px 0 $important;
	padding: 4px 40px $important;
	font-size: 22px $important;
	font-style: italic $important;
	line-height: 1.2em $important;
	text-align: center;
	quotes: none;
	box-sizing: border-box;
	border: none $important;
	color: $default-text-color $important;
	
	> * {
		color: $additional-heading-color;
		font-family: $default-text-font;
		font-size: inherit !important;
		font-style: inherit !important;
		line-height: inherit !important;
		margin: 0;
	}
	
	&:before,
	&:after {
		content: '';
	}
	
	cite,
	.wp-block-quote__citation,
	.wp-block-pullquote__citation,
	footer {
		display: block $important;
		margin-top: 10px $important;
		text-align: inherit $important;
		font-size: 14px $important;
		line-height: 1.3em $important;
		letter-spacing: 0 $important;
		font-style: normal $important;
		font-weight: 400 $important;
		text-transform: none $important;
	}
	
	.wp-block-pullquote__citation {
		margin-top: 5px $important;
	}
}

// layout mixins - end

// checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;

%checkbox-style {
	position: relative;
	margin: 8px 0;
	line-height: 1;
	
	input[type=checkbox] {
		width: $checkbox-size;
		height: $checkbox-size;
		max-height: $checkbox-size;
		position: relative;
		display: inline-block;
		vertical-align: top;
		top: 0;
		left: 0;
		margin: 0;
	}
	
	input[type=checkbox] + label {
		position: absolute;
		top: 0;
		left: 0;
		display: inline-block;
		line-height: 0;
		pointer-events: none;
		cursor: pointer;
	}
	
	input[type=checkbox] + label span.mkdf-label-text {
		display: inline-block;
		padding-left: 10px;
		line-height: $checkbox-size;
		color: $default-heading-color;
	}
	
	input[type=checkbox] + label .mkdf-label-view {
		display: inline-block;
		vertical-align: top;
		width: $checkbox-size;
		height: $checkbox-size;
		background-color: $default-background-color;
		border: $checkbox-border-width solid $default-border-color;
		border-radius: 2px;
		cursor: pointer;
		box-sizing: border-box;
		
		&:hover {
			cursor: pointer;
		}
	}
	
	input[type=checkbox] + label .mkdf-label-view:after {
		content: '';
		position: absolute;
		top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
		left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
		width: $checkbox-size / 2 - $checkbox-border-width;
		height: $checkbox-size / 2 - $checkbox-border-width;
		background-color: $first-main-color;
		opacity: 0;
		transition: opacity 0.3s ease-in-out;
	}
	
	input[type=checkbox]:checked + label .mkdf-label-view:after {
		opacity: 1;
	}
}

// checkbox mixins - end

