// common mixins - start

@mixin mkdfBckImageStyle() {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
}

@mixin mkdfImageOverlayHoverStyle($with-hover: true) {
	
	@if ($with-hover) {
		
		&:hover {
			
			&:after {
				opacity: 1;
			}
		}
		
		&:after {
			@include mkdfAbsoluteHolderLayout();
			content: '';
			background-color: rgba($default-heading-color, .4);
			opacity: 0;
			transition: opacity .2s ease-in-out;
		}
		
	} @else {
		@include mkdfAbsoluteHolderLayout();
		content: '';
		background-color: rgba($default-heading-color, .4);
		opacity: 0;
		transition: opacity .2s ease-in-out;
	}
}

@mixin mkdfStandardPaginationStyle($list_type: null) {
	@include mkdfRelativeHolderLayout(top);
	margin: 9px 0 90px;
	clear: both;
	
	@include ipad-landscape {
		margin: 8px 0 38px;
	}
	
	@include phone-landscape {
		margin: 13px 0 43px;
	}
	
	ul {
		@include mkdfRelativeHolderLayout(top);
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;
		
		li {
			position: static;
			display: inline-block;
			vertical-align: top;
			margin: 5px 13px;
			
			a {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0;
				padding: 0 6px;
				font-family: $default-heading-font;
				font-size: 17px;
				font-weight: 400;
			}
			
			&:hover {
				a {
					color: $default-text-color;
				}
			}
			
			&.mkdf-pag-active {
				
				a {
					color: $default-text-color;
				}
			}
			
			&.mkdf-pag-prev,
			&.mkdf-pag-next {
				a {
					color: $default-text-color;
					
					span {
						transition: .3s ease-out;
					}
				}
			}
			
			&.mkdf-pag-prev,
			&.mkdf-pag-next,
			&.mkdf-pag-first,
			&.mkdf-pag-last {
				position: relative;
				top: -3px;
				margin: 0 2px;
				
				a {
					font-size: 24px;
					
					span {
						display: block;
						line-height: inherit;
					}
				}
			}
			
			&.mkdf-pag-prev {
				float: left;
				
				&:hover {
					content: none;
					
					a span {
						transform: translateX(-4px) translateZ(0);
					}
				}
			}
			
			&.mkdf-pag-next {
				float: right;
				
				&:hover {
					content: none;
					
					a span {
						transform: translateX(4px) translateZ(0);
					}
				}
			}
			
			@if ($list_type == 'shortcode') {
				
				&.mkdf-pag-prev {
					
					a {
						opacity: 0;
					}
				}
				
				&.mkdf-pag-next {
					
					a {
						opacity: 1;
					}
				}
				
			} @else if ($list_type == 'shop') {
				
				span {
					position: relative;
					display: inline-block;
					vertical-align: top;
					margin: 0;
					padding: 0 6px;
					font-family: $default-heading-font;
					color: $default-heading-color;
					font-size: 17px;
					font-weight: 400;
				}
				
				a {
					padding: 0 5px;
					
					&.next,
					&.prev {
						position: absolute;
						padding: 0;
						top: -3px;
						transition: .3s ease-out;
					}
					
					&.next {
						right: 0;
						
						&:hover{
							transform: translateX(4px) translateZ(0);
						}
					}
					
					&.prev {
						left: 0;
						
						&:hover{
							transform: translateX(-4px) translateZ(0);
						}
					}
				}
				
				span,
				a {
					&.current {
						color: $default-text-color;
					}
				}
			}
		}
	}
}

@mixin mkdfButtonDefaultStyle() {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: auto;
	margin: 0;
	font-family: $default-heading-font;
	font-size: 12px;
	line-height: 2em;
	font-weight: 400;
	text-transform: uppercase;
	letter-spacing: .2em;
	border-radius: 0;
	outline: 0;
	cursor: pointer;
	-webkit-appearance: none;
	box-sizing: border-box;
	transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
}

@mixin mkdfButtonSize($size: medium) {
	
	@if ($size == 'small') {
		padding: 11px 29px;
		
	} @else if ($size == 'medium') {
		padding: 13px 48px;
		
	} @else if ($size == 'large') {
		padding: 13px 58px;
		
	} @else if ($size == 'huge') {
		display: block;
		width: 100%;
		text-align: center;
		padding: 13px 26px;
	}
}

@mixin mkdfButtonTransparentColor() {
	color: $default-heading-color;
	background-color: transparent;
}

@mixin mkdfButtonSolidColor($important: null) {
	color: #fff $important;
	background-color: $default-heading-color $important;
	border: 1px solid $default-heading-color $important;
}

@mixin mkdfButtonSolidHoverColor($important: null) {
	color: $default-heading-color $important;
	background-color: transparent $important;
	border: 1px solid $default-heading-color $important;
}

@mixin mkdfButtonOutlineColor($important: null) {
	color: $default-heading-color $important;
	background-color: transparent $important;
	border: 1px solid $default-heading-color $important;
}

@mixin mkdfButtonOutlineHoverColor($important: null) {
	color: #fff $important;
	background-color: $default-heading-color $important;
	border-color: $default-heading-color $important;
}

@mixin qodefButtonSearchIconStyle($color: null, $hover-color: null, $position: null) {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 100%;
    padding: 0;
    border: 0;

    @if ($position == inside) {
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        background-color: transparent;
    }

    @if ($color) {
        color: $color;
    }

    &:hover {

        @if ($hover-color) {
            color: $hover-color;
        }
    }

    svg {
        position: relative;
        display: block;
        width: 25px;
        height: auto;
    }
}

@mixin mkdfPlaceholder {
	&::-webkit-input-placeholder {
		@content
	}
	
	&:-moz-placeholder {
		@content;
		
		opacity: 1;
	}
	&::-moz-placeholder {
		@content;
		
		opacity: 1;
	}
	&:-ms-input-placeholder {
		@content
	}
}

// common mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// mixins styles - start

%input-style {
	position: relative;
	width: 100%;
	margin: 0 0 #{$input-margin}px;
	padding: #{$input-vertical-padding}px #{$input-horizontal-padding}px;
	font-family: $default-text-font;
	font-size: 18px;
	font-weight: 500;
	font-style: italic;
	line-height: calc(#{$input-height}px - (#{$input-vertical-padding * 2}px - 2px));
	color: $additional-heading-color;
	background-color: transparent;
	border: 1px solid $default-border-color;
	border-radius: 0;
	outline: 0;
	cursor: text;
	-webkit-appearance: none;
	box-sizing: border-box;
	transition: border-color 0.2s ease-in-out;
	
	&:focus {
		color: $default-heading-color;
		border-color: $default-text-color;
	}
	
	@include mkdfPlaceholder {
		color: inherit;
	}
}

// mixins styles - end

