// heading mixins - start

@mixin mkdfH1() {
	font-family: $default-heading-font;
	font-size: 40px;
	line-height: 1.200em;
	letter-spacing: .05em;
	text-transform: uppercase;
	color: $default-heading-color;
}

@mixin mkdfH2() {
	font-family: $default-heading-font;
	font-size: 30px;
	line-height: 1.267em;
	letter-spacing: .05em;
	text-transform: uppercase;
	color: $default-heading-color;
}

@mixin mkdfH3() {
	font-family: $default-heading-font;
	font-size: 24px;
	line-height: 1.208em;
	letter-spacing: .045em;
	text-transform: uppercase;
	color: $default-heading-color;
}

@mixin mkdfH4() {
	font-family: $default-heading-font;
	font-size: 17px;
	line-height: 1.353em;
	letter-spacing: .075em;
	text-transform: uppercase;
	color: $default-heading-color;
}

@mixin mkdfH5() {
	font-family: $default-heading-font;
	font-size: 15px;
	line-height: 1.136em;
	letter-spacing: .075em;
	text-transform: uppercase;
	color: $default-heading-color;
}

@mixin mkdfH6() {
	font-family: $default-heading-font;
	font-size: 12px;
	line-height: 1.917em;
	letter-spacing: .075em;
	text-transform: uppercase;
	color: $default-heading-color;
}

@mixin mkdfHeadingStyle($with-heading: null, $with-color: null) {
	font-weight: 400;
	
	@if ($with-heading == 'h1') {
		@include mkdfH1();
	} @else if ($with-heading == 'h2') {
		@include mkdfH2();
	} @else if ($with-heading == 'h3') {
		@include mkdfH3();
	} @else if ($with-heading == 'h4') {
		@include mkdfH4();
	} @else if ($with-heading == 'h5') {
		@include mkdfH5();
	} @else if ($with-heading == 'h6') {
		@include mkdfH6();
	}
	
	@if ($with-color) {
		color: $default-heading-color;
	}
}

@mixin mkdfDefaultHeadingStyle() {
	@include mkdfHeadingStyle();
	margin: 25px 0;
	
	-ms-word-wrap: break-word;
	word-wrap: break-word;
	
	a {
		@include mkdfTypographyLayout();
		
		&:hover {
			color: $first-main-color;
		}
	}
}

@mixin mkdfBody() {
	font-family: $default-text-font;
	font-size: 17px;
	font-weight: 500;
	line-height: 23px;
	color: $default-text-color;
	background-color: $default-background-color;
	-webkit-font-smoothing: antialiased;
}

@mixin mkdfInfoStyle($font-size: 14px, $with-color: true) {
	font-family: $default-text-font;
	font-size: $font-size;
	line-height: 20px;
	
	@if ($with-color) {
		color: $default-heading-color;
	}
}

// heading mixins - end

// background gradient
@mixin bg-gradient($color1: #fffcfc, $color2: #ffedea) {
	background-image: linear-gradient($color1, $color2);
}

//underline

@mixin mkdfUnderline{
	content:'';
	position: absolute;
	display: block;
	width: 0;
	height: 1px;
	background-color: currentColor;
	left: 0;
	bottom: 1px;
	transition: width .3s ease-in-out;
}

//gradient overlay

@mixin mkdfGradientOverlay {
	&:after{
		@include mkdfAbsoluteHolderLayout();
		content: '';
		background-color: rgba($default-heading-color, .4);
		transition: transform .3s ease-in-out;
		background: rgb(247, 222, 217);
		background: linear-gradient(0deg, rgba(247, 222, 217, .5) 0%,rgba(247, 222, 217,.1) 35%, rgba(247, 222, 217, 0) 55%);
		transform: scaleY(0);
		transform-origin: bottom;
	}
	
	&:hover{
		&:after{
			transform: scaleY(1);
		}
	}
}

@mixin mkdfZoomImage{
	img{
		transform: scale(1.01);
		transition: transform .4s ease-out;
	}
	
	&:hover{
		img{
			transform: scale(1.04);
		}
	}
}

